.main-container{
    width: 400px;
    height: 280px;
    display: flex;
    align-items: center;
}

.window{
    height: 100%;
    width: 100%;
    border-radius: 20px;
    overflow: hidden;
}

.all-pages-container{
    height: 100%;
    display: flex;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}

.arrow{
    cursor: pointer;
}